import React, { Component, Fragment } from 'react'
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel
} from 'react-accessible-accordion'

class ChooseUsTab extends Component {
  render () {
    return (
      <>
        <div className='base_header_2'>
          <h3>Varfr vlja oss?</h3>
        </div>
        <Accordion preExpanded={['a']}>
          <AccordionItem uuid='a'>
            <AccordionItemHeading>
              <AccordionItemButton>
                Vad r vrt vrdefrslag?
              </AccordionItemButton>
            </AccordionItemHeading>

            <AccordionItemPanel>
              Tillfredsstlla vra kunders frvntningar p kortast mjliga tid.
            </AccordionItemPanel>
          </AccordionItem>

          <AccordionItem>
            <AccordionItemHeading>
              <AccordionItemButton>
                Vilka vrderingar upprtthller oss?
              </AccordionItemButton>
            </AccordionItemHeading>

            <AccordionItemPanel>
              ppenhet, rlighet, excellens och nrhet till kunden.
            </AccordionItemPanel>
          </AccordionItem>

          <AccordionItem>
            <AccordionItemHeading>
              <AccordionItemButton>
                Vad skiljer oss frn resten?
              </AccordionItemButton>
            </AccordionItemHeading>

            <AccordionItemPanel>
              Vi r knda fr att erbjuda olika tjnster som krvs av varje kunds behov. P detta stt anstller vra kunder ett enda fretag som ansvarar fr ndvndiga tjnster p ett integrerat stt.
            </AccordionItemPanel>
          </AccordionItem>
        </Accordion>
      </>
    )
  }
};

export default ChooseUsTab
